.faculties_course{
  width: 100%;
  float: left;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  position: relative;
  h3{
    font-family: $cyrlight;
    color: #93462a;
    font-size:38px;
    text-transform: uppercase;
    text-align: center;
  }
  .line{
    display: block;
    width: 259px;
    height: 1px;
    background-color: #4c4547;
    margin-bottom:10px;
    margin: 0 auto;
  }
  .faculties_course-items{
    margin-bottom:100px;
    overflow: hidden;
    width:100%;
    float: left;
    position: relative;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    .faculties_course-item{
      background-image: url("/img/hexagon.png");
      background-position: 49% 46px;
      background-repeat: no-repeat;
      overflow: hidden;
      z-index: 1;
      margin-top: 50px;
      position: relative;
      width:33.3%;
      float: left;
      height:580px;
      padding: 0 15px;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      text-align: center;
      .item-border{
        position: relative;
        bottom: -146px;
        display: block;
        float: left;
        width:100%;
        height: 370px;
        border: 2px solid #dfdec0;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
      }
      span{
        width: 100%;
        display: block;
      }
      h4, h5, span, a{
        font-family: $cyrlight;
        color: #4c4547;
      }
      h4{
        color: #4c4547;
        font-size: 18px;
        text-transform: uppercase;
        margin: 0;
        margin-bottom:10px;
      }
      h5{
        font-size: 15px;
        text-transform: uppercase;
        color: #4c4547;
        margin-top: 0;
        margin-bottom:13px;
      }
      .time{
        margin-top: 140px;
        font-size: 15px;
        font-weight:bold;
      }
      .to-who, .benefit{
        font-size: 14px;
        font-weight: 100;
        margin-top:22px;
      }
      p{
        font-size: 17px;
        width: 80%;
        margin: 0 auto;
        color: #4c4547;
      }
      .course-img{
        position: absolute;
        top: 192px;
        left: 54%;
        z-index: 1;
        width:80vw;
      }

      //
      $size: 30vw;

      .wrapper {
        position: relative;
        height: $size;
        width: $size;
        background: rgba(0, 0, 0, .2);
        overflow: hidden;
        margin: 5vh auto;
      }

      .w1 {
        position: absolute;
        top: 34%;
        left: -20%;
        height: $size / 1.73205081;
        width: $size;
        transform: translateY(-50%) rotate(120deg);
        overflow: hidden;
        z-index:1;
      }

      .w2 {
        position: absolute;
        top: 50%;
        left: 0;
        height: $size / 1.73205081;
        width: $size;
        transform: translateY(-50%) rotate(-60deg);
        overflow: hidden;
      }

      .w3 {
        position: absolute;
        top: 50%;
        left: 0;
        height: $size / 1.73205081;
        width: $size;
        transform: translateY(-50%) rotate(-60deg);
        overflow: hidden;
      }

      img {
        position: absolute;
        top: 50%;
        left: 50%;
        //height: $size * 2 / 1.73205081;
        //width: $size * 2 / 1.73205081;
        transform: translateX(-50%) translateY(-50%);
        //filter: blur(1px);
      }

      .more-link{
        display: block;
        height: 43px;
        margin-top: 10px;
        padding-top: 13px;
        color: #a84525;
        text-decoration: underline;
        font-weight:bold;
        border-top: 2px solid #dfdec0;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        &:hover{
          cursor: pointer;
          color: white;
          background-color: #a84525;
        }
      }
      .benefit-content{
        overflow: hidden;
        height: 60px;
      }
    }
  }

}
.bot-bg{
  display: block;
  width: 100%;
  height:820px;
  position: absolute;
  bottom: 0;
  background: url("/img/bot_bg.png");
  background-position: 20% 0;
  z-index: -1;
}
.follow{
  display: block;
  margin: 0 auto;
  width: 397px;
  height: 41px;
  border: 1px solid #93462a;
  text-align: center;
  text-transform: uppercase;
  font-size: 17px;
  font-family:$cyrlight;
  color: #93462a;
  margin-bottom: 34px;
  padding-top: 9px;
  box-sizing: border-box;
  cursor: pointer;
  z-index: 8;
}
.clearfix{
  float: none;
  clear: both;
}