.about_us{
  width: 100%;
  float: left;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  position: relative;
  .faculties-item{
    width: 50%;
    height: 514px;
    float: left;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    overflow: hidden;
    position: relative;
    .triangle_right{
      width: 0;
      height: 0;
      border-top: 50px solid transparent;
      border-left: 40px solid white;
      border-bottom: 50px solid transparent;
      z-index: 2;
      position: absolute;
      bottom:240px;
    }
    .triangle_left{
      width: 0;
      height: 0;
      border-top: 50px solid transparent;
      border-right: 40px solid white;
      border-bottom: 50px solid transparent;
      z-index: 2;
      position: absolute;
      bottom:240px;
      right: 0;
    }
  }
  //text
  .faculties-item:nth-child(2),.faculties-item:nth-child(3),.faculties-item:nth-child(6){
    padding-top: 55px;
    padding-right: 60px;
    padding-left: 60px;
    h3{
      font-family: $cyrlight;
      font-size:28px;
      color: #93462a;
      text-transform: uppercase;
      margin-top:0;
      font-weight:bold;
      line-height: 30px;
    }
    .line{
      display: block;
      width: 259px;
      height: 1px;
      background-color: #4c4547;
      margin-bottom:10px;
    }
    h4{
      font-family: $cyrlight;
      color: #4c4547;
      font-size: 18px;
      font-weight: 400;
      text-transform: uppercase;
    }
    p{
      font-family: $cyrlight;
      color: #4c4547;
      font-size: 17px;
      font-weight: 400;
      line-height: 22px;
    }
    a{
      font-family: $cyrlight;
      color: #93462a;
      font-size: 17px;
      font-weight: 400;
      line-height: 22px;
      text-decoration: underline;
      font-weight:bold;
      margin-top:33px;
    }

  }
  .faculties-item:nth-child(3),.faculties-item:nth-child(5){
    text-align: right;
    .line{
      float: right;
    }
  }
  .faculties-item:nth-child(4){
    text-align: left;
    float: left;
  }
  //images
  .faculties-item:nth-child(2),.faculties-item:nth-child(3),.faculties-item:nth-child(6){
    span{
      display: block;
      position: relative;
      width: 100%;
      height: 514px;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

}
.clearfix{
  clear: both;
  float: none;
}




